.img-notFound{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100vw;
    height: 80vh;
}

.img-notFound a{
    text-decoration: none;
    color: $primaryColor;
}

.img-notFound img{
    width: 100%;
    height: 50%;
    margin: 50px 0;
}

.text-notFound {
    text-align: center;
    font-size: 22px;
    letter-spacing: 1.5px;
    color: $background;
    font-weight: 500;
}